
.container{
	width: 1280px;
	margin: 0 auto;
}
@media only screen and (max-width: 1280px) {
	width: 100%;
}
.check-step{
	width: 100%;
}
.check-step ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin: 25px 0 50px 0;
}
.check-step li.cur {
	border-color: #EE7A23;
    color: #EE7A23;
}
.check-step li.cur:after {
    background: #EE7A23;
}
.check-step ul li {
    position: relative;
    text-align:center;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	width: 25%;
	font-size: 20px;
	line-height: 1.5em;
	padding: 0 1em 1em;
	border-bottom: 2px solid #ccc;
}
.check-step li::after{
    position: absolute;
    content: '';
    bottom: -7px;
    left: 50%;
    margin-left: -7px;
	width: 14px;
	height: 14px;
	border-radius: 50%;
	background: #ccc;
}
.checkout-title{
    position: relative;
	margin: 0 0  41px 0;
	text-align: center;
}
.checkout-title span{
    position: relative;
    text-align: center;
	font-size: 20px;
	line-height: 20px;
	font-weight: bold;
	padding: 0 20px;
	z-index: 20;
	background:#fff;
}
.checkout-title:after{
	position: absolute;
	top: 50%;
	left: 0;
	content: '';
	width: 100%;
	height: 1px;
	background: #ccc;
	z-index:0;
}
.addr-list-wrap{
	width: 100%;
	margin-bottom: 50px;
}
.addr-list {
	width: 100%;
	overflow: hidden;
	margin-bottom: 30px;
}
.addr-list ul {
	width: 100%;
}
.addr-list ul li {
    position: relative;
    float: left;
	width: 23.5%;
	display: inline-block;
	margin: 10px 2% 10px 0;
	text-align: left;
	padding: 20px 20px 40px;
	box-sizing: border-box;
	border: 2px solid #e9e9e9;
	cursor: pointer;
}
.addr-list ul li:hover {
    border-color:#ee7a23;
}
.addr-list ul li.check {
	border-color:#ee7a23;
}
.addr-list ul li:nth-child(4n) {
	margin-right: 0;
}
.addr-list ul li dl dt {
	font-size: 18px;
	font-weight: 100;
	color:#666;
	margin-bottom: 10px;
}
.addr-list ul li dl dd {
    margin-bottom: 6px;
    line-height: 20px;
}
.addr-list ul li .address {
    font-size: 14px;
	height: 40px;
	overflow: hidden;
}
.addr-list ul li .tel {
	font-size: 14px;
}
.addr-list li .addr-opration {
    position: absolute;
    display: none;
}
.addr-list li.check .addr-opration {
    display: block;
}
.addr-list ul li .addr-edit{
    top: 20px;
    right: 20px;
    width: 20px;
    height: 20px;
}
.addr-list ul li .addr-del{
    bottom: 20px;
    right: 20px;
    width: 20px;
    height: 20px;
}
.addr-list ul li .addr-default,.addr-set-default{
	bottom: 20px;
    left: 20px;
    font-size: 14px;
    color:#ee7a23;
}
.addr-list ul li .addr-set-default i{
	font-style: normal;
	color:#ee7a23;
}
.addr-list li .addr-opration .icon {
    width: 100%;
    height: 100%;
}
.addr-list li .addr-opration .icon:hover{
	fill:#ee7a23;
}
.addr-list li.addr-new{
	text-align: center;
}
.addr-list li.addr-new .add-new-inner {
    padding-top: 20px;
}
.addr-list li.addr-new .add-new-inner p{
    padding-top: 12px;
}
.addr-list li.addr-new .add-new-inner .icon-add {
    display: inline-block;
    width: 50px;
    height: 50px;
}
.shipping-method-wrap {
	width: 100%;
	margin-bottom: 40px;
}
.shipping-method {
	width: 100%;
	text-align: center;
}
.shipping-method li{
    display: inline-block;
	width: 300px;
	padding: 20px;
	margin: 5px;
	border: 2px solid #e9e9e9;
	box-sizing: border-box;
	color: #999;
}
.shipping-method li>div {
	line-height: 20px;
}
.shipping-method li .name{
	margin-bottom: 8px;
}
.shipping-method li .price{
	font-weight: 600;
}
.shipping-method li.check{
	border-color: #ee7a23;
	color: #000;
}
.shipping-method li:hover{
	border-color: #ee7a23;
}
.i-up-down {
    position: relative;
    display: inline-block;
    width: 14px;
    height: 6px;
    vertical-align: middle;
    z-index: 2;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.i-up-down i {
    position: absolute;
    /* top: 0; */
    width: 8px;
    height: 1px;
    background: #EE7A23;
}
.i-up-down .i-up-down-l {
    left: 0;
    -webkit-transform: rotate(40deg);
            transform: rotate(40deg);
    /* transition: transform .3s ease-out; */
}
.i-up-down:hover .i-up-down-l{
	-webkit-transform: rotate(-40deg);
	        transform: rotate(-40deg);
	transition: all .3s ease-out;
}
.i-up-down .i-up-down-r {
    right: 0;
    -webkit-transform: rotate(-40deg);
            transform: rotate(-40deg);
    /* transition: transform .3s ease-out; */
}
.i-up-down:hover .i-up-down-r{
	-webkit-transform: rotate(40deg);
	        transform: rotate(40deg);
	transition: all .3s ease-out;
}
.up-down-btn {
    color: #EE7A23;
}



